                          <p>(2) 50%<span class="cash">{{ price * 0.5 }}</span><span class="cash">{{ price * 0.5 | intToChinese }}</span>)</p>
                        </div>
                        <p class="indent">1.2 <input class="short" type="text" /><input class="short" type="text" /><input class="short" type="text" /><input class="short" type="text" /><input class="short" type="text" /><input class="short" type="text" /> <span class="cash">1111</span> </p>
