<script>
export default {
  name: 'agreement',
  data () {
    return {
      list: ['', '', '', '', ''],
      listco: ['', '', '', '', ''],
      price: 0
    }
  },
  mounted () {
    this.price = this.$route.params.price
    // console.log(this.$route.params.price)
  },
  filters: {
    intToChinese (str) {
      let mnum = parseFloat(str)
      let strOutput = '', strTemp = '', strInTemp = ''
      let unitArray = new Array('', '', '')
      let mnumArray = mnum.toString().split('.')
      let integralnum = mnumArray[0]
      let integrallen = integralnum.length
      let decimalnum = (mnum.toString().indexOf('.') >= 0) ? mnumArray[1].substr(0, 2) : '0'
      let decimallen = decimalnum.length
      let ints = parseInt(integrallen / 4)
      let inty = integrallen % 4
      if (ints > 3 || (ints === 3 && inty > 0)) return ''
      if (inty > 0) {
        ints++
        integralnum = '0000'.substr(inty) + integralnum
        integrallen = integralnum.length
      }
      let i = 0
      while (i < integrallen) {
        let strOutTemp = ''
        strTemp = integralnum.substr(i, 4)
        i += 4
        for (var j = 0; j < 4; j++) {
          strInTemp = parseInt(strTemp.substr(j, 1))
          strOutTemp += unitArray[2].substr(strInTemp, 1)
          if (strInTemp > 0 && j < 3) strOutTemp += unitArray[1].substr(j, 1)
        }
        strOutTemp = strOutTemp.replace(/+$/, '')
        ints--
        if (strOutTemp !== '') strOutTemp += unitArray[0].substr(ints, 1)
        if (strTemp.substr(3,1) === '0') strOutTemp += ''
        strOutput += strOutTemp
      }
      strOutput = strOutput.replace(/+/g, '').replace(/^/, '').replace(/$/, '')
      if (strOutput === '') strOutput = ''
      if (decimallen === 2) {
        strOutput += (decimalnum.charAt(0) !== '0') ? unitArray[2].substr(parseInt(decimalnum.charAt(0)), 1) + '' : ''
        if (strOutput === '') strOutput = ''
        strOutput += unitArray[2].substr(parseInt(decimalnum.charAt(1)), 1) + ''
      } else {
        if (decimalnum !== '0') strOutput += unitArray[2].substr(parseInt(decimalnum), 1) + ''
        if (strOutput !== '') strOutput += ''
      }
      if (strOutput === '') strOutput = ''
      return strOutput
    }
  }
}
</script>

<style lang="less">
